  font-size: .85em;
  overflow-y: auto;
}

.log-entry, .trace-entry {
  border-bottom: 1px dashed #eee;
  padding: 5px 0;
}

.log-entry .timestamp, .trace-entry .timestamp {
  color: #7f8c8d;
  margin-right: 10px;
  font-size: .9em;
}

.log-entry .log-message-INFO {
  font-weight: normal;
}

.log-entry .level-WARN {
  color: #f39c12;
}

.log-entry .details, .trace-entry .params {
  color: #555;
  margin-left: 20px;
  font-size: .95em;
}

#logs-filter, #trace-filter {
  background-color: #f9f9f9;
  border: 1px solid #eee;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px;
