#logs-filter label, #trace-filter label {
  margin-right: 5px;
}

#logs-filter select, #logs-filter input, #trace-filter input {
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-right: 15px;
  padding: 5px;
}

#status-content li strong {
  width: 150px;
  display: inline-block;
}

.tabs {
  border-bottom: 1px solid #ccc;
  margin-bottom: 20px;
  overflow: hidden;
}

.tab-button {
  background-color: inherit;
  float: left;
  cursor: pointer;
  border: none;
  border-radius: 5px 5px 0 0;
  outline: none;
  padding: 14px 16px;
  font-size: 17px;
  transition: all .3s;
}

.tab-button:hover {
  background-color: #ddd;
}

.tab-button.active {
  background-color: #ccc;
  border-bottom: 2px solid #2c3e50;
}

.tab-content {
  border-top: none;
  padding: 6px 12px;
  display: none;
}

.tab-content.active {
  display: block;
}

